@import "vars";
@import "mixins";

* {
    margin: 0;
    padding: 0;
    font-family: $anonPro;
}

body {
    width: 100%;
    height: 100%;
    background: #ede8e8;
    text-align: center;
    font-size: 20px;
}

h1 {
    font-family: $allerta;
    font-size: 60px
}

nav {
    width: 100%;
    height: 60px;
    background-color: #364968;
    color: lightgrey;
    text-align: center;
    @include box-shadow(0 5px 4px 1px rgba(0, 0, 0, 0.4));
    @include transition(top 500ms, top 500ms);
}

.nav {
    z-index: 999;
    height: 60px;

    .flex {
        height: 60px;
    }

    div {
        font-size: 60px;
        font-weight: normal;
        margin: 0 10px 0 10px;
        a {
            @include box-sizing(border-box);
            text-transform: uppercase;
            height: 60px;
            text-decoration: none;
            @include transition(background 0.3s,
            background 0.3s);
            line-height: 60px;
            display: block;
            @include allLinkColor(lightgrey, #51637f);
        }
    }
}

.flex {
    @include flexDisplay;
    @include justify-content(center);
}

#btn {
    width: 80px;
    height: 80px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1001;

    svg {
        width: 60px;
        height: 60px;

        line {
            stroke: black;
            stroke-width: 2.5px;
            stroke-linecap: round;
        }
    }
}



#close {
    @include transition(opacity 300ms, opacity 300ms);
}

.sidenav {
    text-align: left;
    left: 0;
    position: fixed;
    height: 100%;
    width: 0;
    z-index: 1000;
    background: -webkit-gradient(linear, left top, right top, from(rgba(180, 174, 174, 0.9)), to(rgba(198, 192, 192, 0.4)));
    background: -o-linear-gradient(left, rgba(180, 174, 174, 0.9), rgba(198, 192, 192, 0.4));
    background: linear-gradient(to right, rgba(180, 174, 174, 0.9), rgba(198, 192, 192, 0.4));
    overflow: hidden;
    @include transition(width 1s, width 1s);

    h1 {
        margin-top: 70px;
    }

    button {
        z-index: 1009;
        background: none;
        display: block;
        width: 100%;
        height: 50px;
        border: none;
        margin: 5px 0;
        cursor: pointer;
        white-space: nowrap;
        text-align: center;
        @include transition(background 500ms, background 500ms);
    }

    button:hover {
        background: rgb(167, 160, 160);

    }

    button a {
        display: block;
        width: 100%;
        height: 100%;
    }
}



#wrapper {
    width: 100%;
    text-align: center;
}

@include parallaxBG;

.section {
    margin: 0 auto;
    background: #ede8e8;
    width: 100%;
}

footer {
    font-size: 20px;
    height: 50px;
    background: #364968;
    text-align: center;
    width: 100%;
    margin-top: 60px;
}

.copyright {
    font-size: 35px;
    font-family: $allerta;
    a {
        color: #ede8e8;
        text-decoration: none;
    }
}

.niceInput {
    margin: 3px;
    text-align: center;
    background: lightgrey;
    width: 250px;
    height: 40px;
    padding: 2.5px 5px;
    border-radius: 20px;
    border: 1.5px solid grey;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}